.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #2c3e50;
  color: #ecf0f1;
  font-family: 'Arial', sans-serif;
  padding: 20px;
}

h1 {
  display: flex;
  justify-content: center;
  gap: 10px;
  flex-wrap: wrap;
  margin-bottom: 40px;
}

h1 span {
  display: inline-block;
  background-color: #ecf0f1;
  color: #2c3e50;
  padding: 15px;
  border-radius: 5px;
  font-weight: bold;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  text-align: center;
  font-size: 1.5rem;
  border: 3px solid #2c3e50;
}

h1 span::before {
  content: '';
  position: absolute;
  top: -10px;
  left: -10px;
  width: 100%;
  height: 100%;
  background-color: #34495e;
  z-index: -1;
  transform: rotate(-5deg);
  border-radius: 5px;
  border: 3px solid #34495e;
}

.clock-container {
  position: relative;
  width: 350px;
  height: 350px;
  border: 4px solid #ecf0f1;
  border-radius: 50%;
  background-color: #34495e;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}

.number {
  position: absolute;
  font-size: 1.25rem;
  font-weight: bold;
  color: #ecf0f1;
}

.clock-hand {
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform-origin: bottom center;
}
